import React from "react";
import Skills from "./Skills";
import OtherSkills from "./OtherSkills";
import styled from "styled-components";
import Color from "../Color";

const Wrapper = styled.div`
    display: flex;
    gap: 40px;
    align-items: flex-start;
    flex-wrap: nowrap;
`;

const SkillsContainer = styled.div`
    flex: 1 1 auto;
    min-width: 300px;
    background-color: ${Color.toAlpha(Color.second, 0.3)};
    border: 1px solid ${Color.second};
    border-radius: 24px;
    margin: 20px;
`;

const OtherSkillsContainer = styled.div`
    margin: 16px;
    padding: 12px;
    border-radius: 16px;
    background: ${Color.toAlpha(Color.second, 0.3)};
    display: flex;
    justify-content: center;
    border: 1px solid ${Color.second};
`

const SkillsSection = ({ skills }) => (
    <section>
        <OtherSkillsContainer>
            <OtherSkills items={[
                "MySQL", "PostgreSQL", "MongoDB", "Redis",
                "Json", "YAML", "TOML", "Git",
                "Docker", "Pterodactyl", "Kubernetes",
                "REST API", "GraphQL", "gRPC",
                "Node.js", "Express.js", "NestJS", "Spring Boot", "Django",
                "React", "Vue", "Next.js",
                "Prometheus", "Grafana", "Sentry"
            ]} />
        </OtherSkillsContainer>
        <Wrapper>
            <SkillsContainer>
                <Skills skills={skills} />
            </SkillsContainer>
        </Wrapper>
    </section>
);

export default SkillsSection;