import React from "react";
import styled from "styled-components";
import Color from "../Color";

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    max-width: 1000px;
    margin: auto;
    margin-top: 30px;
    background: ${Color.toAlpha(Color.secondHovered, 0.7)};
    border: 1px solid ${Color.secondHovered};
    border-radius: 20px;
    text-align: center;
    font-size: 18px;
    perspective: 1000px;
    position: relative;
    overflow: visible;
    backdrop-filter: blur(15px);

    @media (max-width: 768px) {
        flex-direction: column;
        text-align: center;
    }
`;

const Image = styled.img`
    width: 300px;
    height: auto;
    border-radius: 20px;
    margin-right: 20px;
    
    @media (max-width: 768px) {
        margin-right: 0;
        margin-bottom: 20px;
    }
`;

const Text = styled.div`
    font-size: 18px;
    color: ${Color.border};
    line-height: 1.6;
    max-width: 600px;
`;

const ImageTextBlock = ({ imageSrc, text }) => {
    return (
        <Wrapper>
            <Image src={imageSrc} alt="Image" />
            <Text>{text}</Text>
        </Wrapper>
    );
};

export default ImageTextBlock;