import React, { useState } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import Color from "../Color";

const HeaderWrapper = styled.header`
    margin: 10px;
    align-self: center;
    text-align: center;
    padding: 30px 10px;
    font-size: 16px;
    color: ${Color.border};
    background: ${Color.toAlpha(Color.main, 0.5)};
    border: 1px solid ${Color.light(Color.main)};
    backdrop-filter: blur(20px);
    background-size: 50% 50%;
    border-radius: 16px;
    width: 50%;
`;

const Title = styled(Link)`
    font-size: 42px;
    margin: 0;
    font-weight: normal;
    transition: 0.3s ease;
    text-decoration: none;
    color: ${Color.border};
    padding: 6px;
    border-radius: 16px;

    &:hover {
        cursor: pointer;
        background-color: ${Color.main};
    }
`;

const ButtonsContainer = styled.div`
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 15px;
`;

const HeaderButton = styled(Link)`
    color: ${Color.border};
    text-decoration: none;
    padding-inline: 25px;
    padding-block: 15px;
    font-size: 16px;
    border-radius: 20px;
    border: 1px solid ${Color.secondHovered};
    background: ${Color.second};
    transition: 0.3s;
    position: relative;
    display: inline-block;

    &:hover {
        background-color: ${Color.light(Color.second)};
        border-color: ${Color.light(Color.secondHovered)};
    }
`;


const Header = ({ title }) => {
    return (
        <HeaderWrapper>
            <Title to="/">{title}</Title>
            <ButtonsContainer>
                <HeaderButton to="/skills">
                    Навыки
                </HeaderButton>
                <HeaderButton to="/works">
                    Работы
                </HeaderButton>
            </ButtonsContainer>
        </HeaderWrapper>
    );
};

export default Header;