import React, { useState } from "react";
import styled from "styled-components";
import Color from "../Color";

const FrameworkWrapper = styled.div`
    margin-top: 10px;
    padding: 10px;
    background: ${Color.second};
    border: 1px solid${Color.secondHovered};
    border-radius: 20px;
    transition: 0.3s;
    position: relative;
    overflow: visible;
    backdrop-filter: blur(15px);
`;

const FrameworkTitle = styled.h4`
    font-size: 16px;
    margin-bottom: 8px;
    color:${Color.inverted};
`;

const FrameworkItem = styled.div`
    font-size: 14px;
    padding: 5px 0;
    color: ${Color.border};
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;

    &:last-child {
        border-bottom: none;
    }

    &:hover .tooltip {
        opacity: 1;
        visibility: visible;
    }
`;

const Tooltip = styled.div`
    position: absolute;
    background: rgba(5, 6, 29, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.5);
    color:${Color.border};
    padding: 8px;
    backdrop-filter: blur(5px);
    border-radius: 10px;
    font-size: 12px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s ease;
   ${Color.border}-space: pre-wrap;
    z-index: 10;
    pointer-events: none;

    ${({ isVisible, coords }) =>
            isVisible &&
            `
        opacity: 1;
        visibility: visible;
        left: ${coords.x + 10}px;
        top: ${coords.y + 20}px;
    `}
`;

const FrameworkList = ({ frameworks }) => {
    const [coords, setCoords] = useState({ x: 0, y: 0 });

    const handleMouseMove = (e) => {
        const rect = e.currentTarget.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;
        setCoords({ x, y });
    };

    return (
        <FrameworkWrapper onMouseMove={handleMouseMove}>
            <FrameworkTitle>Дополнительно</FrameworkTitle>
            {frameworks.map((framework, index) => (
                <FrameworkItem key={index}>
                    {typeof framework === "string" ? (
                        framework
                    ) : (
                        <>
                            {framework.name}
                            {framework.description && (
                                <Tooltip
                                    className="tooltip"
                                    style={{
                                        left: coords.x + 10 + "px",
                                        top: coords.y - 100 + "px",
                                    }}
                                >
                                    {framework.description}
                                </Tooltip>
                            )}
                        </>
                    )}
                </FrameworkItem>
            ))}
        </FrameworkWrapper>
    );
};

export default FrameworkList;
