import React, { useState } from "react";
import styled from "styled-components";
import FrameworkList from "./FrameworkList";
import Color from "../Color";

const SkillBoxStyled = styled.div`
    background: ${Color.toAlpha(Color.secondHovered, 0.5)};
    border-radius: 20px;
    padding: 20px;
    margin: 10px;
    text-align: center;
    font-size: 18px;
    transition: 0.3s;
    perspective: 1000px;
    position: relative;
    overflow: visible;
    backdrop-filter: blur(15px);

    h3 {
        font-size: 22px;
        margin-bottom: 8px;
        color: ${Color.border};
    }

    p {
        color: #aaaaaa;
        font-size: 14px;
    }
`;

const SkillBox = ({ skill }) => {
    return (
        <SkillBoxStyled>
            <h3>{skill.name}</h3>
            <p>{skill.description}</p>
            {skill.frameworks && <FrameworkList frameworks={skill.frameworks} />}
        </SkillBoxStyled>
    );
};

const SkillsWrapper = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 20px;
    justify-content: center;
    gap: 20px;
`;

const Skills = ({ skills }) => (
    <div>
        <SkillsWrapper>
            {skills.map((skill, index) => (
                <SkillBox key={index} skill={skill} />
            ))}
        </SkillsWrapper>
    </div>
);

export default Skills;